.interview-steps-container {
  .interview-steps-section {
    .interview-header {
      .jadu-title {
        font-size: 40px;
        font-weight: 700;
        margin: auto;
        margin-bottom: 23px;
        display: flex;
        justify-content: center;
        width: fit-content;
        position: relative;
        .do-span {
          color: #6f177b;
          margin-right: 10px;
        }
        .jadu-span {
          color: #4d4ab3;
        }
        .arrow-right-icon {
          font-size: 61px;
          position: absolute;
          right: -24%;
          top: -20px;
          color: #6f177b;
        }
      }
      .interview-level {
        font-weight: 500;
        text-align: center;
        color: #6f177b;
        font-weight: 600;
        text-align: center;
        text-transform: uppercase;
      }
    }
    .interview-steps-body {
      padding: 30px 20px;
      padding-bottom: 0;
    }
  }
}
